import uniPopup from "@/components/uni-popup/uni-popup.vue"
import Loadings from '../loading/loading.vue'
import requireurl from '../../requist/requist.js'
export default {
	components: {
		uniPopup,
		Loadings
	},
	data() {
		return {
			BarHeight: 0,
			TabList: [],
			LoadingS: true,
			enterInfo: '',
			timer: '01-01',
			Mounth: 1,
			Day: 1,
			Year: 1937,
			BlueImages: require('../../static/img/bluemsg.png'),
			BlueImg: require('../../static/img/bluelogo.png'),
			BlueTsk: require('../../static/img/tsk.png'),


			RedImages: require('../../static/img/red.png'),
			RedImg: require('../../static/img/hong.png'),
			RedTsk: require('../../static/img/redtsk.png'),

			GreenImages: require('../../static/img/green.png'),
			GreenImg: require('../../static/img/lv.png'),
			GreenTsk: require('../../static/img/greentsk.png'),

			YellowImages: require('../../static/img/yellow.png'),
			YellowImg: require('../../static/img/huang.png'),
			YellowTsk: require('../../static/img/yellowtsk.png'),


			BlueRun: require('../../static/img/running.png'),
			Norun: require('../../static/img/norun.png'),
			OKRun: require('../../static/img/success.png'),

			triggered: true
		}
	},
	onShow() {
		this.getTimer()
		this.GetDay()
		
		console.log('显示')
	},
	methods: {
		upper() {
			console.log('To the bottom')
		},
		lower() {
			console.log('to the top')
		},
		open(val) {
			console.log(val)
			this.enterInfo = val.info ? val.info : val.enter ? val.enter.info : '暂无内容';
			// this.enterInfo = val.enter.info ? val.enter.info : val.info ? val.info : '暂无内容';
			this.$refs.popup.open()
		},
		tasklist(res) {
			this.clearLoading()
			this.TabList = res.data
			console.log(this.TabList)
		},
		errtask(err) {
			console.log(err)
		},
		//修改日报
		ChangeTaskInfo(val) {
			if (!this.TodayChangeTask) {
				wx.showToast({
					title: '仅今日任务可操作',
					icon: 'none'
				})
				return
			}
			var _that = this;
			console.log(val)
			if (val.enter.id) {
				uni.setStorage({
					key: 'info',
					data: val,
					success: function() {
						_that.AddTaskInfo('change')
					}
				});
			} else {
				let obj = {
					TaskIds: val.id,
					status: val.enter.status,
					task_name: val.task_name,
					project: val.project
				}
				uni.setStorage({
					key: 'taskid',
					data: obj,
					success: function() {
						_that.AddTaskInfo('change')

					}
				});
			}
		},
		AddTask() {
			this.AddTaskInfo('add')
		},
		//新增日报
		AddTaskInfo(val) {
			uni.navigateTo({
				url: '../newtask/newtask?name=' + val
			});
		},
		//天数减一
		BackTimer() {
			if (this.Day > 1) {
				this.Day--
			} else {
				if (this.Mounth > 1) {
					this.Mounth--
					if (this.Mounth == 1 || this.Mounth == 3 || this.Mounth == 5 || this.Mounth == 7 || this.Mounth == 8 || this.Mounth ==
						10 || this.Mounth == 12) {
						this.Day = 31
					} else if (this.Mounth == 2) {
						if (this.Year % 4 == 0) {
							this.Day = 29
						} else {
							this.Day = 28
						}
					} else {
						this.Day = 30
					}
				} else {
					this.Year--
					this.Day = 31
					this.Mounth = 12
				}
			}
			this.GetDay()
		},
		AddTimer() {
			if (this.Mounth == 2) {
				if (this.Year % 4 == 0) {
					if (this.Day > 28) {
						this.Day = 1;
						this.Mounth++;
						return;
					} else {
						this.Day++;
						return;
					}
				} else {
					if (this.Day > 27) {
						this.Day = 1;
						this.Mounth++;
						return;
					} else {
						this.Day++;
						return;
					}
				}
			} else if (this.Mounth == 1 || this.Mounth == 3 || this.Mounth == 5 || this.Mounth == 7 || this.Mounth == 8 || this
				.Mounth == 10 || this.Mounth == 12) {
				if (this.Day > 30) {
					if (this.Mounth == 12) {
						this.Year++;
						this.Mounth = 1;
						this.Day = 1;
					} else {
						this.Mounth++;
						this.Day = 1;
					}
				} else {
					this.Day++;
				}

			} else {
				if (this.Day > 29) {
					this.Mounth++;
					this.Day = 1;
				} else {
					this.Day++;
				}
			}
			this.GetDay()
		},
		getTimer() {
			var timestamp2 = new Date();
			var _that = this
			console.log(this)

			_that.Year = timestamp2.getFullYear();
			_that.Mounth = timestamp2.getMonth() + 1;
			_that.Day = timestamp2.getDate();

		},
		showLoading() {
			this.LoadingS = true
		},
		clearLoading() {
			this.LoadingS = false
		},
		// YesToy(item){
		// 	if(item.status == 1){
		// 		return 1;
		// 	}else if(item.status == 2){
		// 		return 2
		// 	}else if(item.status == 3){
				
		// 	}
		// },
		bbbbbbbaa(item){
			if (!item.enter) {
				return;
			}
			if(item.enter.status  == 1){
				return 1;
			}else if (item.enter.status == 3) {
				return 5;
			}else if(item.enter.status == 2){
				return 7;
			}
		},
		
		asdasdasd(item) {
			if (!item.enter) {
				return;
			}
			// if(item.enter.status  == 1){
			// 	return 1;
			// }else 

			if (item.enter.status == 3) {
				return 5;
			}

			var timers = new Date(item.plan_end_time_).getTime() / (1000 * 60 * 60 * 24) - new Date().getTime() / (
				1000 * 60 * 60 * 24)
			if (timers < 0) {
				//逾期
				return 2;
			} else if (timers > 2) {
				return 3;
				//正常
			} else {
				return 4;
				//警告
			}

		},
		GetDay() {
			this.showLoading()
			var timestamp2 = new Date();
			var years = timestamp2.getFullYear();
			var Months = timestamp2.getMonth() + 1;
			var Days = timestamp2.getDate();
			console.log(timestamp2)
			console.log(years)
			console.log(Months)
			console.log(Days)
			var timesss = new Date(`${this.Year}/${this.Mounth}/${this.Day} 00:00:00`).getTime();
			var timess = new Date(`${years}/${Months}/${Days} 00:00:00`).getTime();
			console.log(timesss)
			console.log(timess)
			console.log(timesss >= timess)
			if (timesss >= timess) {
				requireurl.getData(`p/v1/wx/today/?date=${this.Year}-${this.Mounth}-${this.Day}`, this.tasklist, this.errtask)
				this.TodayChangeTask = true;
				console.log('今日及其以后')
			} else {
				requireurl.getData(`p/v1/wx/journal/?day=${this.Year}-${this.Mounth}-${this.Day}`, this.tasklist, this.errtask)
				this.TodayChangeTask = false
				console.log('其他日')
			}
		}
	},
	created() {
		// requireurl.getData('p/v1/wx/today/',this.tasklist,this.errtask)
		var _that = this
		wx.getSystemInfo({
			success: function(res) {
				_that.BarHeight = res.statusBarHeight
			}
		});
	},
	computed: {

	},
	watch: {

	},
}
